<template>
<div>
  <group>
    <group-item
    title="Popup"
    right-desc=""
    @click="click1"></group-item>
    <group-item
    title="Popup"
    right-desc=""
    @click="click2"></group-item>
    <group-item
    title=""
    :right-desc="picker.value.join()"
    @click="triggerPickerShow"></group-item>
  </group>

  <popup :show.sync="show1" height="200px">
    <p>html</p>
  </popup>
  <popup :show.sync="show2" height="100%">
    <p @click="click2"></p>
  </popup>
  <popup :show.sync="picker.show">
    <flexbox class="test_btns_wrap">
      <flexbox-item class="btn_1" @click="triggerPickerShow">1</flexbox-item>
      <flexbox-item class="btn_2" @click="triggerPickerShow">2</flexbox-item>
    </flexbox>
    <picker :data='picker.data' :columns=3 :value.sync='picker.value'></picker>
  </popup>
</div>
</template>

<script>
import { Popup, Group, GroupItem, Picker, Flexbox, FlexboxItem } from '../components/'

export default {
  components: {
    Popup,
    Group,
    GroupItem,
    Picker,
    Flexbox,
    FlexboxItem
  },
  methods: {
    click1 () {
      this.show1 = !this.show1
    },
    click2 () {
      this.show2 = !this.show2
    },
    triggerPickerShow () {
      this.picker.show = !this.picker.show
    }
  },
  data () {
    return {
      show1: false,
      show2: false,
      picker: {
        show: false,
        value: ['uk', 'liverpool', ''],
        data: [{
          name: '',
          value: 'china',
          parent: 0
        }, {
          name: '',
          value: 'uk',
          parent: 0
        }, {
          name: '',
          value: 'china001',
          parent: 'china'
        }, {
          name: '',
          value: 'china002',
          parent: 'china'
        }, {
          name: '',
          value: 'china001002',
          parent: 'china001'
        }, {
          name: '',
          value: 'china002001',
          parent: 'china002'
        }, {
          name: '',
          value: 'london',
          parent: 'uk'
        }, {
          name: '',
          value: 'liverpool',
          parent: 'uk'
        }, {
          name: '',
          value: '',
          parent: 'london'
        }, {
          name: '',
          value: '',
          parent: 'liverpool'
        }]
      }
    }
  }
}
</script>

<style>
.test_btns_wrap {
  background-color: #fff;
  text-align: center;
}
.btn_1 {
  color: blue;
}
</style>



