<template>
<div>
  <group title="Action Sheet">
    <group-item
    title="1"
    right-desc=""
    @click="triggerActionSheet1"></group-item>
    <group-item
    title=""
    right-desc=""
    @click="triggerActionSheet2"></group-item>
    <group-item
    title="Classes"
    right-desc=""
    @click="triggerActionSheet3"></group-item>
  </group>

  <action-sheet
  :show.sync="actionSheet.show"
  :buttons="actionSheet.buttons"
  :cancel-text="actionSheet.cancelText"></action-sheet>
</div>
</template>

<script>
import { ActionSheet, Group, GroupItem } from '../components/'

export default {
  components: {
    ActionSheet,
    Group,
    GroupItem
  },
  methods: {
    triggerActionSheet1 () {
      let _this = this
      let _actionSheet = this.actionSheet
      _actionSheet.buttons = [{
        name: '',
        onClick () {
          _this.actionSheet.show = false
        }
      }]
      _actionSheet.cancelText = '' // 
      _this.actionSheet.show = !this.actionSheet.show
    },
    triggerActionSheet2 () {
      let _this = this
      let _actionSheet = this.actionSheet
      _actionSheet.buttons = [{
        name: '1',
        onClick () {
          _this.actionSheet.show = false
        }
      }]
      _actionSheet.cancelText = ''
      _this.actionSheet.show = !this.actionSheet.show
    },
    triggerActionSheet3 () {
      let _this = this
      let _actionSheet = this.actionSheet
      _actionSheet.buttons = [{
        name: '',
        classes: ['your_class1', 'your_class2'],
        onClick () {
          _this.actionSheet.show = false
        }
      }, {
        name: '2',
        classes: 'default',
        onClick () {
          console.log('2')
        }
      }, {
        name: '3',
        onClick () {
          console.log('3')
        }
      }]
      _actionSheet.cancelText = ''
      _this.actionSheet.show = !this.actionSheet.show
    }
  },
  data () {
    return {
      actionSheet: {
        show: false,
        buttons: [{
          name: '',
          classes: '',
          onClick () {
          }
        }],
        cancelText: ''
      }
    }
  }
}
</script>

<style>
</style>



