<template>
<div class="mui_group_item">
  <div class="mui_group_item_bd mui_group_item_primary">
    <p>{{title}}</p>
    <inline-desc v-if="inlineDesc">{{inlineDesc}}</inline-desc>
    <slot name="bd"></slot>
  </div>
  <div class="mui_group_item_ft" :class="{'with_arrow': withArrow}">
    {{rightDesc}}
    <slot name="ft"><slot>
  </div>
</div>
</template>

<script>
import InlineDesc from '../inline-desc/'

export default {
  components: {
    InlineDesc
  },
  props: {
    title: String,
    inlineDesc: String,
    rightDesc: String,
    withArrow: {
      type: Boolean,
      default: true
    }
  },
  created () {
    this.$dispatch('group.class.add', 'mui_group_item_access') // 
  }
}
</script>

<style lang="less">
@import "style.less";
</style>



