<template>
  <div>
    <group title="default format: YYYY-MM-DD">
      <datetime
      :value.sync="value1"
      @change="change"
      title="2016-02-11"></datetime>
    </group>

    <group title="format: HH:II">
      <datetime
      :value.sync="value2"
      format="HH:II"
      @change="change"
      title=""
      ></datetime>
    </group>

    <group title="format: YYYY-MM-DD H:II  inline-desc">
      <datetime
      :value.sync="value3"
      title=""
      inline-desc=""
      @change="change"
      format="YYYY-MM-DD H:II"
      ></datetime>
    </group>

    <group title=" placeholder">
      <datetime
      :value.sync="value4"
      title=""
      @change="change"
      format="YYYY-MM-DD HH:II"
      ></datetime>
    </group>

    <group title=" min-year and max-year">
      <datetime
      :value.sync="value5"
      title="years after 2000"
      @change="change"
      :min-year=2000
      :max-year=2016
      format="YYYY-MM-DD HH:II"
      ></datetime>
    </group>

    <group title="">
      <datetime
      :value.sync="value6"
      title=""
      inline-desc="inline-desc"
      @change="change"
      :min-year=2000
      :max-year=2016
      format="YYYY-MM-DD HH:II"
      year-row="{value}"
      month-row="{value}"
      day-row="{value}"
      hour-row="{value}"
      minute-row="{value}"
      confirm-text=""
      cancel-text=""
      title-text=""
      ></datetime>
    </group>
  </div>
</template>

<script>
import { Datetime, Group } from '../components/'

export default {
  ready () {
  },
  components: {
    Datetime,
    Group
  },
  data () {
    return {
      value1: '2016-02-11',
      value2: '',
      value3: '',
      value4: 'Placeholder',
      value5: '',
      value6: 'Placeholder'
    }
  },
  methods: {
    change (value) {
      console.log('change', value)
    }
  }
}
</script>



