<template>
  <div>
    <group-title></group-title>
    <picker :data='years' :value.sync='year1' @change='change'></picker>

    <group-title></group-title>
    <picker :data='years' :value.sync='year2' @change='change'></picker>

    <group-title></group-title>
    <input v-model='year3'/>
    <picker :data='years' :value.sync='year3' @change='change'></picker>

    <group-title></group-title>
    <picker :data='data4' :value.sync='data4Value' @change='change'></picker>

    <group-title></group-title>
    <picker :data='data5' :columns=3 :value.sync='data5Value' @change='change'></picker>
  </div>
</template>

<script>
import { GroupTitle, Picker } from '../components/'

let years = []
for (var i = 2000; i <= 2030; i++) {
  years.push({
    name: i + '',
    value: i + ''
  })
}
export default {
  ready () {
  },
  components: {
    GroupTitle,
    Picker
  },
  watch: {
    data5Value: {
      handler (val) {
        let str = `val: ${val}` // 
        console.log(str)
      },
      deep: true // 
    }
  },
  data () {
    return {
      years: [years],
      year1: [''],
      year2: ['2013'],
      year3: ['2015'],
      data4: [
        ['', '', ''],
        ['you', 'I', 'him'],
        ['ni', 'wo', 'ta'],
        [1, 2, 3, 4, 5],
        [5, 4, 3, 2, 1]
      ],
      data4Value: ['', 'I', 'ta', 1, 5],
      data5: [{
        name: '',
        value: 'china',
        parent: 0
      }, {
        name: '',
        value: 'uk',
        parent: 0
      }, {
        name: '',
        value: 'china001',
        parent: 'china'
      }, {
        name: '',
        value: 'china002',
        parent: 'china'
      }, {
        name: '',
        value: 'china001002',
        parent: 'china001'
      }, {
        name: '',
        value: 'china002001',
        parent: 'china002'
      }, {
        name: '',
        value: 'london',
        parent: 'uk'
      }, {
        name: '',
        value: 'liverpool',
        parent: 'uk'
      }, {
        name: '',
        value: '',
        parent: 'london'
      }, {
        name: '',
        value: '',
        parent: 'liverpool'
      }],
      data5Value: ['uk', 'liverpool', '']
    }
  },
  methods: {
    change (value) {
      // value
      console.log('change', value)
    }
  }
}
</script>

