<template>
<div>
  <group title="Dialog">
    <group-item
    title="1"
    right-desc=""
    @click="triggerDialog1"></group-item>
    <group-item
    title="2"
    right-desc=""
    @click="triggerDialog2"></group-item>
    <group-item
    title="2"
    right-desc=""
    @click="triggerDialog3"></group-item>
  </group>

  <dialog
  :show="dialog.show"
  :head="dialog.head"
  :content="dialog.content"
  :buttons="dialog.buttons"></dialog>
</div>
</template>

<script>
import { Dialog, Group, GroupItem } from '../components/'

export default {
  components: {
    Dialog,
    Group,
    GroupItem
  },
  methods: {
    triggerDialog1 () {
      let _this = this
      let _dialog = this.dialog
      _dialog.head = 'Dialog'
      _dialog.content = ''
      _dialog.buttons = [{
        name: '',
        onClick () {
          _this.dialog.show = false
        }
      }]
      _this.dialog.show = !this.dialog.show
    },
    triggerDialog2 () {
      let _this = this
      let _dialog = this.dialog
      _dialog.head = 'Dialog'
      _dialog.content = '<span style="color:red;">2</span>'
      _dialog.buttons = [{
        name: '',
        classes: 'default',
        onClick () {
          _this.dialog.show = false
        }
      }, {
        name: '2',
        classes: 'primary',
        onClick () {
          console.log('2')
        }
      }]
      _this.dialog.show = !this.dialog.show
    },
    triggerDialog3 () {
      let _this = this
      let _dialog = this.dialog
      _dialog.head = 'Dialog'
      _dialog.content = '<p style="text-align:center;">2</p>'
      _dialog.buttons = [{
        name: '',
        classes: ['your_class1', 'your_class2'],
        onClick () {
          _this.dialog.show = false
        }
      }, {
        name: '2',
        classes: 'default',
        onClick () {
          console.log('2')
        }
      }, {
        name: '3',
        classes: 'primary',
        onClick () {
          console.log('3')
        }
      }]
      _this.dialog.show = !this.dialog.show
    }
  },
  data () {
    return {
      dialog: {
        show: false,
        head: '',
        content: '',
        buttons: [{
          name: '',
          classes: '',
          onClick () {
          }
        }]
      }
    }
  }
}
</script>

<style>
</style>



